ransform: uppercase;\n    color: #fff;\n    cursor: pointer;\n}\n\n.intro__wrap-btn {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n}\n\n.intro__wrap-strip {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    max-width: 95px;\n    margin: 50px auto;\n\n    .intro__strip {\n        width: 26px;\n        height: 4px;\n        background: rgba(159, 159, 159, 0.87);\n        border-radius: 10px;\n    }\n\n    .strip-active {\n        background: #fff;\n    }\n}\n\n.wrap_img {\n    width: 213px;\n    height: 232px;\n    margin: 0 auto;\n    overflow: hidden;\n\n    img {\n        max-width: 213px;\n        max-height: 232px;\n        margin: 0 auto;\n        display: inline-block;\n    }\n}\n\n.mt-30 {\n    margin-top: 30%;\n}\n\n.intro__wrap-btn-auth {\n    display: block;\n\n    .intro__btn-login {\n        margin: 0 auto;\n        display: flex;\n        flex-direction: row;\n        justify-content: center;\n        align-items: center;\n        padding: 12px 24px;\n        width: 327px;\n        gap: 10px;\n        background: $violet-light;\n        border-radius: 4px;\n        font-style: normal;\n        font-weight: 400;\n        font-size: 16px;\n        line-height: 150.5%;\n        text-transform: uppercase;\n        color: #fff;\n        cursor: pointer;\n        transition: background .3s;\n\n        &:hover {\n            background: darken($violet-light, 10);\n            transition: background .3s;\n            cursor: pointer;\n        }\n    }\n\n    .intro__btn-reg {\n        margin: 20px auto;\n        box-sizing: border-box;\n        display: flex;\n        flex-direction: row;\n        justify-content: center;\n        align-items: center;\n        padding: 12px 24px;\n        gap: 10px;\n        width: 327px;\n        border: 2px solid #8E7CFF;\n        border-radius: 4px;\n        font-style: normal;\n        font-weight: 400;\n        font-size: 16px;\n        line-height