<script>
export default {
    name: "Intro",
    data: () => ({
        intro: {
            intro1: true,
            intro2: false,
            intro3: false,
            intro4: false,
            intro5: false
        },
    }),
    methods: {
        showIntro(show_item) {
            if (show_item === 'intro1') {
                this.hideIntro1()
            } else {
                this.allIntroFalse()
                this.intro[show_item] = true
            }
        },
        hideIntro1() {
            this.allIntroFalse()
            this.intro.intro1 = true
            setTimeout(() => {
                this.showIntro('intro2')
            }, 1000)
        },
        allIntroFalse() {
            this.intro = {
                intro1: false,
                intro2: false,
                intro3: false,
                intro4: false,
                intro5: false
            }
        },
        skip() {
            this.allIntroFalse()
            this.intro.intro5 = true
        }
    },
    mounted() {
        this.hideIntro1()
        console.log(this.$auth.user().roles[0])
        if(this.$auth.user() && (this.$auth.user().roles[0] === 'Admin' || this.$auth.user().roles[0] === 'Editor')) {
            this.$router.push('/home')
        }
    }
}
</script>

